*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    height: 100vh;
    font-family: 'Poppins';

}
body{
    overflow-x: hidden;
    height: 100%;
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.webgl
{
    position: absolute;
    top: 0;
    left: 0;
    outline: none;
    /* mix-blend-mode: exclusion; */
    z-index: -1;

}
.container{
    height: 100vh;
    display:flex ;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    gap:10vh
}

.button-87 {
  margin: 10px;
  padding: 15px 30px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  border-radius: 10px;
  display: block;
  border: 0px;
  font-weight: 700;
  box-shadow: 0px 0px 14px -7px #f09819;
  background-image: linear-gradient(45deg, #0d1265 0%, #550666  51%, #110115  100%);
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-87:hover {
  background-position: right center;
  /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

.button-87:active {
  transform: scale(0.95);
}
.img{
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    align-content: flex-end;
    flex-wrap: wrap;
    height: 10vh;
}
.img1,.img2{
    height: 100%;
}
h1{
    font-size: 4vw;
    text-transform: uppercase;
    color: white;
}
h1.h-b{
    padding-top: 5vh;
    color: black;
}

section{
    height: 100vh;
    text-align: center;
}
/* //stykes of image */
.styles{
    height: 10vh;
    display: flex;

}
.left{
    width: 50%;
}
.right{
    width: 50%;
}
img{
    width: 100%;
    height: 100%;
}

